@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
.header{position:fixed;display:flex;align-items:center;justify-content:space-between;z-index:1000;width:calc(360px - 30px);height:60px;padding:0 15px}.header .metamask-icon{cursor:pointer}.header .metamask-icon:hover{opacity:0.9}.info{margin-top:10px}

.eth-border{width:344px;height:172px;margin:8px 8px 0;box-shadow:0 4px 0 0 #444956;border:solid 1px #444956;color:#ffffff;text-shadow:0px 4px 1px rgba(255,255,255,0.3);font-family:'Share Tech Mono', monospace;font-size:14px;font-style:normal;line-height:1.26;letter-spacing:0.4px}.eth-border.actions{display:flex;flex-wrap:wrap;height:140px;width:328px;padding:16px 8px}.eth-border .amount{display:flex;justify-content:space-between;align-items:center;width:134px;height:54px;padding:0 12px;background:#2e3340;border:1px solid #a9acb1}.eth-border .amount .image-wrap{display:flex;align-items:center}.eth-border .amount .title{color:#b6b7bc;width:90px;padding-right:10px}.eth-border .amount img{height:22px;width:22px}.eth-border .border-wrap{display:inline-block;width:144px;height:56px;padding-left:16px;margin-left:8px;text-shadow:none}.eth-border .border-wrap .title{margin-top:6px;color:white}.eth-border .border-wrap .number{color:#1659f8;font-size:22px}.eth-border .button{display:flex;justify-content:center;align-items:center;padding:0;width:156px;height:48px}.eth-border .button.eth{border:1px solid #ff5100;background-color:rgba(255,81,0,0.56);box-shadow:0 6px 0 0 rgba(255,81,0,0.3)}.eth-border .button.claim{margin-left:8px;border:1px solid #1958f8;background-color:rgba(25,88,248,0.56);box-shadow:0 6px 0 0 rgba(25,88,248,0.3)}.base-background{background-image:url(/rock3t-frontend/static/media/layout.afb15684.svg);position:absolute;z-index:1;height:100vh}.bordered-data{display:inline-block;color:white;height:82px;width:170px;background-image:url(/rock3t-frontend/static/media/dataBorder.24fffdf6.svg)}.bordered-data .title,.bordered-data .value{text-shadow:0px 4px 1px rgba(255,255,255,0.3);margin:18px 0 0 18px}.bordered-data .value{margin:14px 0 0 18px}.rocket-container{display:flex;justify-content:center;position:relative;z-index:2;height:236px;margin-top:50px}.rocket-container img{height:236px}

body{margin:0;padding:0}.center{text-align:center}.button{padding:5px 10px;border-radius:2px;cursor:pointer}.button.cancel{color:white;background:rgba(255,0,0,0.7)}.button:hover{opacity:0.8}a{text-decoration:none}

